@charset "UTF-8";
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
}

html {
  font-family: "Tajawal", sans-serif;
  font-size: 62.2%;
  box-sizing: border-box;
}
@media screen and (max-width: 989px) {
  html {
    font-size: 50%;
  }
}

body {
  display: grid;
  grid-template-columns: [full-start] minmax(2rem, 1fr) [center-start] repeat(8, [col-start] minmax(min-content, 15rem) [col-start]) [center-end] minmax(2rem, 1fr) [full-end];
}

.main {
  grid-column: full-start/full-end;
}

.container-z {
  display: grid;
  grid-template-columns: [full-start] minmax(2rem, 1fr) [center-start] repeat(8, [col-start] minmax(min-content, 15rem) [col-start]) [center-end] minmax(2rem, 1fr) [full-end];
}

ul, li {
  list-style: none;
}

.nav {
  grid-column: center-start/center-end;
  display: grid;
  grid-template-columns: 170px 1fr 100px;
}
@media (max-width: 845px) {
  .nav {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
.nav__logo {
  width: 150px;
  margin: auto;
}
.nav__logo img {
  width: 100%;
}
.nav__search {
  display: flex;
  align-items: center;
  margin-top: 20px;
  cursor: pointer;
}
@media (max-width: 845px) {
  .nav__search {
    justify-content: flex-end;
  }
}
.nav__search img {
  width: 44px;
}

.custom-wordpres-dropdown {
  display: flex;
  align-items: center;
  width: 100%;
  margin-top: 20px;
}
.custom-wordpres-dropdown .menu-header-menu-container, .custom-wordpres-dropdown .menu-header-menu-english-container {
  width: 100%;
}
.custom-wordpres-dropdown #menu-header-menu, .custom-wordpres-dropdown #menu-header-menu-english {
  display: flex;
  width: 100%;
  justify-content: space-around;
}
.custom-wordpres-dropdown #menu-header-menu li, .custom-wordpres-dropdown #menu-header-menu-english li {
  margin-left: 5px;
}
.custom-wordpres-dropdown #menu-header-menu li.current-menu-item, .custom-wordpres-dropdown #menu-header-menu-english li.current-menu-item {
  border-bottom: 2px solid #F5776E;
}
.custom-wordpres-dropdown #menu-header-menu a, .custom-wordpres-dropdown #menu-header-menu-english a {
  color: #00577F;
  font-size: 2.4rem;
  font-weight: 600;
  text-decoration: none;
  border-bottom: 0px solid transparent;
  transition: all 0.3s ease-in-out;
}
.custom-wordpres-dropdown #menu-header-menu a:hover, .custom-wordpres-dropdown #menu-header-menu-english a:hover {
  border-bottom: 2px solid #F5776E;
}

.hero {
  grid-column: full-start/full-end;
}
.hero__cover {
  height: 490px;
}
.hero__cover img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: 0 0;
  position: relative;
}
.hero__cover img::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  background-color: red;
}
.hero__content {
  position: absolute;
  top: 0;
  width: 100%;
  height: 490px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 2;
  direction: rtl;
}
.hero__content h2 {
  width: 90%;
  text-align: center;
  margin-bottom: 2rem;
}
.hero__content h2 a {
  font-size: 3.3rem;
  color: #fff;
  text-decoration: none;
}
@media (max-width: 500px) {
  .hero__content h2 a {
    font-size: 2.2rem;
  }
}
.hero__content h2 a:hover {
  text-decoration: underline;
  text-decoration-color: #00577F;
}
.hero__content-excerpt {
  background-color: #f5776eab;
  padding: 18px;
  border-radius: 6px;
  width: 800px;
  text-align: center;
}
@media (max-width: 895px) {
  .hero__content-excerpt {
    width: 90%;
  }
}
.hero__content-excerpt a {
  font-size: 2.7rem;
  color: #fff;
  text-decoration: none;
}
@media (max-width: 500px) {
  .hero__content-excerpt a {
    font-size: 1.8rem;
  }
}
.hero__content-excerpt a:hover {
  text-decoration: underline;
  text-decoration-color: #00577F;
}
.hero__overlay {
  width: 100%;
  height: 100%;
  background-color: #2a3558d9;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
}
.hero .owl-nav {
  position: absolute;
  top: 200px;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  pointer-events: none;
}
@media screen and (max-width: 800px) {
  .hero .owl-nav {
    display: none;
  }
}
.hero .owl-nav .owl-prev {
  pointer-events: all;
  margin-left: 4.3rem !important;
}
.hero .owl-nav .owl-prev:hover {
  background-color: transparent !important;
}
.hero .owl-nav .owl-next {
  pointer-events: all;
  margin-right: 4.3rem !important;
}
.hero .owl-nav .owl-next:hover {
  background-color: transparent !important;
}
.hero .owl-nav .owl-prev,
.hero .owl-nav .owl-next {
  outline: none;
}
.hero .owl-nav .owl-prev img,
.hero .owl-nav .owl-next img {
  width: 50px;
}

.owl-carousel.owl-loaded {
  direction: ltr;
  display: grid !important;
}

